import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import './styles.css'

export default class Hamburger extends PureComponent {
  render () {
    return (
      <a href="#"
        className="hamburger fa-bars"
        onClick={this.props.onOpenDrawer}>
      </a>
    )
  }
}

Hamburger.propTypes = {}



