import React, { Component } from 'react'
import PropTypes from 'prop-types'
import Filter from '../Filters/components/Filter';
import './styles.css'

export default class Mode extends Component {
  constructor (props) {
    super(props)

    this.state = {
      opened: false
    }

    this.toggleView = this.toggleView.bind(this)
  }

  toggleView () {
    this.setState({opened: !this.state.opened})
  }

  render () {
    const classes = [ 'mode' ]
    if (this.state.opened) { classes.push('opened') }

    return (
      <div
        className={classes.join(' ')}
        onMouseEnter={this.toggleView}
        onMouseLeave={this.toggleView}
      >
        <div className="mode__header">
          Оптимальный выбор
        </div>
        <div className="mode__content">
          Эти товары мы подобрали с учетом выбранных вами фильтров. Каждый из них — оптимальное решение в своей ценовой категории.
        </div>
      </div>
    )
  }
}

Mode.propTypes = {
  mode: PropTypes.object,
  onApplyOption: PropTypes.func
}



